import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { Observable, Subscription } from 'rxjs';
import { debounceTime } from 'rxjs/operators';
import { Retailer, RetailerService } from '../retailer.service';

@Component({
  selector: 'app-retailer-left-pane',
  templateUrl: './retailer-left-pane.component.html',
  styleUrls: ['./retailer-left-pane.component.scss']
})
export class RetailerLeftPaneComponent implements OnInit {

  advancedSearchSub: Subscription;

  retailers: any[];
  filteredRetailers: any[];
  loadDataSub: Subscription;
  selectedAction = 'All';
  loadingCollection = false;
  advancedSearchApplied = false;

  page = 1;
  pageSize = 5;
  collectionSize = 0;

  searchControl: FormControl = new FormControl();

  filterAction: string[] = ['All', 'Active', 'Inactive'];

  advancedSearchForm: FormGroup;
  searchedRetailers: Observable<Retailer[]>;

  public userInput = new FormControl();

  constructor(private retailerService: RetailerService,
    private router: Router,
    private route: ActivatedRoute,
    private modalService: NgbModal,
    private fb: FormBuilder) {
  }


  ngOnInit(): void {
    // this.searchForm = this.fb.group({ userInput: '' });
    this.setAdvancedSearchForm();

    this.loadDataSub = this.retailerService.loadRetailerData()
      .subscribe(res => {
        this.retailers = this.retailerService.retailers;
        this.filteredRetailers = this.retailerService.retailers;
        if (this.filteredRetailers) {
          this.collectionSize = this.filteredRetailers.length;
        }
      });

    this.searchControl.valueChanges
      .pipe(debounceTime(200))
      .subscribe(value => {
        this.filerData(value);
        if (this.filteredRetailers) {
          this.collectionSize = this.filteredRetailers.length;
        }
    });

    // Listen for company change
    this.advancedSearchSub = this.retailerService.onAdvancedSearchApplied.subscribe(res => {
      if (res) {
        this.filteredRetailers = res.retailers;
        if (this.filteredRetailers) {
          this.collectionSize = this.filteredRetailers.length;
        }
      }
    });

  }

  filerData(val) {
    status = this.selectedAction.toLowerCase();
    if (val) {
      val = val.toLowerCase();
    } else {
      this.filerDataWithStatus(status);
    }
    const rows = this.retailers.filter(function (d) {
      return (status === 'all' ? d.name.toString().toLowerCase().includes(val) :
        d.name.toString().toLowerCase().includes(val) && d.status.toLowerCase() === status);
    });
    this.filteredRetailers = rows;
    this.collectionSize = this.filteredRetailers.length;
  }

  filerDataWithStatus(val) {

    if (val) {
      val = val.toLowerCase();
    } else {
      return this.filteredRetailers = [...this.retailers];
    }
    const rows = this.retailers.filter(function (d) {
      return d.status.toString().toLowerCase() === val;
    });
    if (val === 'all') {
      this.filteredRetailers = this.retailers;
    } else {
      this.filteredRetailers = rows;
    }
    this.collectionSize = this.filteredRetailers.length;

  }

  getRetailerById(retailerId) {
    console.log('retailerId', retailerId);
    this.retailerService.getRetailerById(retailerId)
      .subscribe(res => {

      }, err => {
        console.log(err);
      });
  }

  filter(action) {
    this.selectedAction = action;
    this.filerDataWithStatus(action.toString());
  }

  add() {
    this.router.navigate(['add'], { relativeTo: this.route });
  }

  setAdvancedSearchForm() {
    this.advancedSearchForm = this.fb.group({
      name: '',
      registrationNumber: '',
      status: ''
    });
  }

  open(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
    .result.then((result) => {
      console.log(result);
    }, (reason) => {
      console.log('Err!', reason);
    });
  }

  performAdvancedSearch(event) {
    this.loadingCollection = true;
    this.modalService.dismissAll('close clicked');
    this.advancedSearchApplied = true;
    // const company: Retailer = this.advancedSearchForm.value;
    // this.companyService.performAdvancedSearch(company)
    //   .subscribe(res => {
    //     this.filteredRetailers = res;this.loadingCollection = false;
    //   },
    //   error => {
    //     console.log(error);
    //   });
  }

  clearSearchCriteria() {
    this.advancedSearchApplied = false;
    this.filerData('');
  }

}
