import { Component, Input, OnDestroy, OnInit, ViewChild, ViewChildren } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar';
import { Subscription } from 'rxjs';
import { SharedAnimations } from 'src/app/shared/animations/shared-animations';
import { Audit, AuditService } from 'src/app/shared/services/audit.service';
import { ChatCollection, User } from '../../../chat/chat.service';
import { Retailer, RetailerService } from '../retailer.service';

@Component({
  selector: 'app-retailer-content',
  templateUrl: './retailer-content.component.html',
  styleUrls: ['./retailer-content.component.scss'],
  animations: [SharedAnimations]
})
export class RetailerContentComponent implements OnInit, OnDestroy {
  user: User = new User();
  activeContact: User = new User();
  activeRetailer: Retailer = new Retailer();
  public chatCollection: ChatCollection;

  userUpdateSub: Subscription;
  chatUpdateSub: Subscription;
  chatSelectSub: Subscription;

  loadAuditDataSub: Subscription;
  retailerSelectSub: Subscription;
  retailerId: number;
  showHistory = true;

  audits: Audit[];


  // tslint:disable-next-line:no-input-rename
  @Input('matSidenav') matSidenav;
  @ViewChild(PerfectScrollbarDirective) psContainer: PerfectScrollbarDirective;

  @ViewChildren('msgInput') msgInput;
  @ViewChild('msgForm') msgForm: NgForm;

  constructor(public retailerService: RetailerService,
    private auditService: AuditService,
    private router: Router,
    private route: ActivatedRoute) {

  }

  ngOnInit() {
    
    this.route.params.subscribe(params => this.retailerId = params['id']);

    console.log('Retriveing id',  this.retailerId );
    // Listen for company change
    this.chatSelectSub = this.retailerService.onRetailerSelected.subscribe(res => {
      if (res) {
        this.activeRetailer = res.retailer;
        this.showHistory = false;
      }
    });

    this.loadAuditDataSub = this.auditService.loadAuditData()
      .subscribe(res => {
        this.audits = this.auditService.audits;
      });

  }

  ngOnDestroy() {
    if (this.userUpdateSub) { this.userUpdateSub.unsubscribe(); }
    if (this.chatSelectSub) { this.chatSelectSub.unsubscribe(); }
    if (this.chatUpdateSub) { this.chatUpdateSub.unsubscribe(); }
    if (this.retailerSelectSub) { this.retailerSelectSub.unsubscribe(); }
  }


  edit(retailer) {
    console.log('open edit : ', retailer);
    this.router.navigate(['edit', retailer.id], { relativeTo: this.route });
  }

  toggleShowAudit() {
    this.showHistory = !this.showHistory;
    if (this.showHistory) {
      this.loadAuditDataSub = this.auditService.loadAuditData()
        .subscribe(res => {
          this.audits = this.auditService.audits;
        });
    }
  }
}
